<template>
  <div>
    <!--  -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" @clear="getOrderList" clearable>
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="">
          <template #default="{ row }">
            <el-tag v-if="row.pay_status === '1'" type="success" size="mini"></el-tag>
            <el-tag v-else type="danger" size="mini"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="">
          <template #default="{ row }">{{row.create_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column label="">
          <template #default="{ row }">
            <el-button @click="showBox(row)" size="mini" icon="el-icon-edit" type="primary"></el-button>
            <el-button
              @click="showProgressBox(row)"
              size="mini"
              icon="el-icon-location"
              type="success"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog @close="addressDialogClosed" title="" :visible.sync="addressVisible" width="50%">
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            v-model="addressForm.address1"
            :options="cityData"
            :props="{ expandTrigger: 'hover' }"
            clearable
            filterable
            popper-class="cascader-height"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="addressVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
import cityData from './citydata'
export default {
  data() {
    return {
      // 
      queryInfo: {
        // 
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },
      // 
      orderList: [],
      // 
      total: 0,
      // 
      addressVisible: false,
      // 
      addressForm: {
        address1: '',
        address2: ''
      },
      addressFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'change' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cityData,
      progressVisible: false,
      // 
      progressInfo: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList() {
      const res = await this.$axios.get('orders', {
        params: this.queryInfo
      })
      if (res._RejCode !== 200) return this.$message.error(res._RejMes)
      this.orderList = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange(val) {
      this.queryInfo.pagesize = val
      this.queryInfo.pagesize = 1
      this.getOrderList()
    },
    // 
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val
      this.getOrderList()
    },
    // 
    showBox(row) {
      this.addressVisible = true
    },
    // 
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    async showProgressBox(row) {
      const res = await this.$axios.get('/kuaidi/1106975712662')
      if (res._RejCode !== 200) return this.$message.error(res._RejMes)
      this.progressInfo = res.data
      console.log(res.data)
      this.progressVisible = true
    }
  }
}
</script>
