<template>
  <div>
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!-- EChartsDom -->
      <div id="main" style="height:520px;"></div>
    </el-card>
  </div>
</template>
<script>
// echarts
import echarts from 'echarts'
import _ from 'lodash'
export default {
  data() {
    return {
      // 
      options: {
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#E9EEF3'
            }
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            boundaryGap: false
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ]
      }
    }
  },
  methods: {
    async getEchartData() {
      const res = await this.$axios.get('reports/type/1')
      if (res._RejCode !== 200) return this.$message.error(res._RejMes)
      return res.data
    }
  },
  // 
  async mounted() {
    //  DOM echarts 
    var myChart = echarts.init(document.getElementById('main'))
    //  domecharts
    const option = await this.getEchartData()
    console.log(option)
    const Result = _.merge(option, this.options)
    // 
    myChart.setOption(Result)
  }
}
</script>
<style lang="scss" scoped>
</style>
